import ListPageHeading from '../../../../containers/customers/ListPageHeading';
import AddNewModal from '../../../../containers/customers/AddNewModal';
import ListPageListing from '../../../../containers/customers/ListPageListing';
import useMousetrap from '../../../../hooks/use-mousetrap';
import { DashboardCentral, ChatObjetivo } from '../../../../Dusk/DashboardDusk';
import Config from '../../../../config/ConfigApi.json';

const getIndex = (value, arr, prop) => {
  for (let i = 0; i < arr.length; i += 1) {
    if (arr[i][prop] === value) {
      return i;
    }
  }
  return -1;
};

