import React from 'react';
import {
  Row,
  Card,
  CardBody,
  CardSubtitle,
  CardImg,
  CardText,
  Badge,
} from 'reactstrap';
import { NavLink } from 'react-router-dom';
import { ContextMenuTrigger } from 'react-contextmenu';
import { Colxx } from '../../components/common/CustomBootstrap';
import { adminRoot } from '../../constants/defaultValues';

const ImageListView = ({ customer, collect, onCheckItem }) => {
  return (
    <Colxx sm="6" lg="4" xl="3" className="mb-3" key={customer.id}>
      <ContextMenuTrigger id="menu_id" data={customer.id} collect={collect}>
        <Card
          onClick={(event) => onCheckItem(event, customer.id)}
        >
          <div className="position-relative">
            <NavLink to={`${adminRoot}/pages/customers/${customer.id}`} className="w-40 w-sm-100">
              <CardImg top alt={customer.title} src={customer.img} />
            </NavLink>
            <Badge
              color={customer.statusColor}
              pill
              className="position-absolute badge-top-left"
            >
              {customer.status}
            </Badge>
          </div>
          <CardBody>
            <Row>
              <Colxx xxs="10" className="mb-3">
                <CardSubtitle>{customer.title}</CardSubtitle>
                <CardText className="text-muted text-small mb-0 font-weight-light">
                  {customer.date}
                </CardText>
              </Colxx>
            </Row>
          </CardBody>
        </Card>
      </ContextMenuTrigger>
    </Colxx>
  );
};

/* React.memo detail : https://reactjs.org/docs/react-api.html#reactpurecomponent  */
export default React.memo(ImageListView);
